.services {
  background-color: $color-tangaroa;
}

.services__wrapper {
  padding: 87px 10px 112px;
  max-width: calc(1170px + 20px);
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    padding: 50px 30px 22px 26px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 18px;
  }
}

.services h2 {
  margin: 0 0 52px;
  font-family: $robotoC;
  font-weight: 400;
  font-size: 36px;
  line-height: 50.63px;
  color: $color-default-white;

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 48px;
  }

  @media (max-width: $mobile-width) {
    font-size: 28px;
    line-height: 39.38px;
    padding-left: 10px;
    margin-bottom: 41px;
  }
}

.services ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0;
  margin-bottom: 0;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    margin-bottom: 16px;
  }
}

.services li {
  position: relative;
  margin-right: 30px;
  margin-bottom: 16px;
  width: calc((100% - 30px - 30px)/3);

  @media (max-width: $tablet-width) {
    width: 100%;
    margin-bottom: 46px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px;
    height: 256px;
    overflow: hidden;
  }
}

.services img {
  width: 100%;
  height: auto;
}

.services li::after {
  position: absolute;
  width: 63px;
  height: 63px;
  background-color: $color-sky-blue;
  color: $color-default-white;
  bottom: 0;
  right: 0;
  content: " ";
  background-image: url("../img/arrow.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  cursor: pointer;
}

.services a {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(27, 45, 55, 0.7);
}

.services a:hover {
  scale: none;
}

.services li:hover {
  transform: scale(1.05);
  transition: transform 300ms ease-in;
}


.services h3 {
  display: block;
  width: 40%;
  margin: 50px 0 0 44px;
  font-family: $robotoC;
  font-weight: normal;
  font-style: 20px;
  line-height: 30px;
  color: $color-default-white;

  @media (max-width: $tablet-width) {
    margin-top: 43px;
    width: 20%;
  }

  @media (max-width: $mobile-width) {
    margin-top: 23px;
    margin-left: 35px;
    font-size: 20px;
    line-height: 30px;
    width: 60%;
  }
}

.services li:nth-child(3n) {
  margin-right: 0;
}
